<template>
  <div class="back">
    <v-app-bar app elevation="2" color="#333744" dark>
      <v-app-bar-nav-icon @click="this.$router.back()">
        <v-icon v-html="'mdi-arrow-left'"></v-icon>
      </v-app-bar-nav-icon>
      <v-toolbar-title>
        <span class="pr-8">编辑科研成果</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
    </v-app-bar>
    <app-bar
      back-icon
      title="编辑科研成果"
      hide-user-menu
      hideMessage
      hideDemand
    ></app-bar>
    <v-content style="min-height: calc(100vh - 64px)">
      <v-container>
        <v-card class="mx-auto mb-12 mt-6" max-width="1000" min-height="2850">
          <v-form class="pl-8" :model="form">
            <div class="pt-8">
              <v-icon>mdi-tag</v-icon><span class="ml-3">项目名称</span>
            </div>
            <v-col cols="12" sm="6">
              <v-text-field
                required
                placeholder="请输入项目名称，20个字以内"
                :rules="[v => !!v || '项目名称为必填项']"
                v-model="form.entryname"
              ></v-text-field>
            </v-col>
            <div class="pt-8">
              <v-icon>mdi-card-account-details</v-icon
              ><span class="ml-3">成果负责人</span>
            </div>
            <v-col cols="12" sm="6">
              <v-text-field
                required
                placeholder="请输入成果负责人姓名"
                v-model="form.charge"
                :rules="[v => !!v || '成果负责人姓名为必填项']"
              ></v-text-field>
              <v-text-field
                required
                placeholder="请输入成果负责人职称信息"
                :rules="[v => !!v || '成果负责人职称信息为必填项']"
                v-model="form.position"
              ></v-text-field>
            </v-col>
            <div class="mt-5">
              <v-icon>mdi-clipboard-text</v-icon
              ><span class="ml-3">科研成果介绍</span>
            </div>
            <v-col cols="12" sm="8">
              <v-textarea
                v-model="form.results"
                filled
                placeholder="请输入科研成果简介，200字以内"
                :rules="[v => !!v || '项目名称为必填项']"
              ></v-textarea>
            </v-col>
            <div class="mt-5">
              <v-icon>mdi-cube-scan</v-icon><span class="ml-3">技术创新点</span>
            </div>
            <div style="height: 610px;">
              <quill-editor
                class="mr-8"
                max-height="400"
                v-model="form.tecNew"
                ref="myQuillEditor"
                style="height: 500px;"
                :config="editorOption"
              ></quill-editor>
            </div>
            <div class="mt-5">
              <v-icon>mdi-elevation-rise</v-icon
              ><span class="ml-3">市场前景及应用</span>
            </div>
            <div style="height: 610px;">
              <quill-editor
                class="mr-8"
                max-height="400"
                v-model="form.future"
                ref="myQuillEditor"
                style="height: 500px;"
                :config="editorOption"
              ></quill-editor>
            </div>
            <div class="mt-5">
              <v-icon>mdi-electron-framework</v-icon
              ><span class="ml-3">技术成熟度</span>
            </div>
            <v-radio-group v-model="form.maturity" row>
              <v-radio class="ml-4" value="概念验证" label="概念验证"></v-radio>
              <v-radio class="ml-4" value="原理样机" label="原理样机"></v-radio>
              <v-radio class="ml-4" value="工程样机" label="工程样机"></v-radio>
              <v-radio class="ml-4" value="中试" label="中试"></v-radio>
              <v-radio class="ml-4" value="产业化" label="产业化"></v-radio>
            </v-radio-group>
            <div class="mt-5">
              <v-icon>mdi-phone</v-icon><span class="ml-3">技术成熟度</span>
            </div>
            <v-col cols="12" sm="6">
              <v-text-field
                required
                filled
                label="项目联系人姓名"
                placeholder="请输入联系人姓名"
                :rules="[v => !!v || '联系人姓名为必填项']"
                v-model="form.contacts"
              ></v-text-field>
              <v-text-field
                required
                filled
                label="项目联系人电话"
                placeholder="请输入联系电话"
                :rules="[v => !!v || '联系人电话为必填项']"
                v-model="form.tel"
              ></v-text-field>
              <v-text-field
                required
                filled
                label="项目联系邮箱"
                placeholder="请输入联系邮箱"
                :rules="[v => !!v || '联系人邮箱为必填项']"
                v-model="form.mail"
              ></v-text-field>
              <v-text-field
                required
                filled
                label="个人信息"
                placeholder="请输入个人联系方式"
                :rules="[v => !!v || '个人信息为必填项']"
                v-model="form.information"
              ></v-text-field>
            </v-col>
            <div class="mt-5">
              <v-icon>mdi-file-document-edit</v-icon
              ><span class="ml-3">科研成果论文</span>
            </div>
            <el-upload
              class="upload-demo mt-5 mr-5"
              drag
              action="https://jsonplaceholder.typicode.com/posts/"
              multiple
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                将文件拖到此处，或
                <em>点击上传</em>
              </div>
              <div class="el-upload__tip" slot="tip">
                只能上传pdf文件，且不超过20M
              </div>
            </el-upload>
            <v-row class="mt-12 mb-12">
              <div class="mr-12 ml-3">
                <v-btn
                  width="120px"
                  depressed
                  large
                  color="info"
                  @click="saveChanges()"
                  :loading="loading"
                  :disabled="loading"
                  >提交修改</v-btn
                >
              </div>
              <div class="mr-12">
                <v-btn
                  width="120px"
                  class="white--text"
                  depressed
                  large
                  color="#AAAAAA"
                  @click="cancel()"
                  >取消</v-btn
                >
              </div>
            </v-row>
          </v-form>
        </v-card>
      </v-container>
    </v-content>
    <app-footer></app-footer>
  </div>
</template>

<script>
import { quillEditor, Quill } from 'vue-quill-editor'
import { container, ImageExtend, QuillWatch } from 'quill-image-extend-module'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import AppFooter from '@/components/App/AppFooter'

Quill.register('modules/ImageExtend', ImageExtend)

export default {
  name: 'SchoolEdit',
  components: {
    AppFooter,
    quillEditor
  },
  props: ['id'],
  watch: {
    id: function() {
      this.getSci()
    }
  },
  data() {
    return {
      loading: false,
      content: null,
      editorOption: {
        placeholder: '请输入',
        theme: 'snow',
        modules: {
          ImageExtend: {
            loading: true,
            name: 'img',
            size: 3
          },
          toolbar: {
            container: container,
            handlers: {
              image: function() {
                QuillWatch.emit(this.quill.id)
              }
            }
          }
        }
      },
      form: {
        id: this.id,
        entryname: '',
        tel: '',
        mail: '',
        position: '',
        information: '',
        charge: '',
        results: '',
        maturity: '',
        tecNew: null,
        future: null
      }
    }
  },
  methods: {
    clearInput() {
      this.form = {}
    },
    async getSci() {
      let res = await this.$http.get('/api/school/readMySci?id=' + this.id)
      if (res.data.code != 200) {
        this.$message.warning('抱歉，您无法编辑该科研成果！')
        return false
      }
      this.form = res.data.data.Sci
    },
    async saveChanges() {
      let res = await this.$http.post('/api/school/updateSchool', this.form, {
        headers: { 'Content-Type': 'application/json' }
      })
      if (res.data.code === 200) {
        this.$message.success('修改科研成果内容成功！')
        this.clearInput()
        this.$router.push('/university/history')
      } else {
        this.clearInput()
        this.$message.error('修改科研成果内容失败！')
      }
      this.clearInput()
    },
    cancel() {
      this.clearInput()
      this.$router.push('/university/history')
    }
  },
  mounted() {
    this.getSci()
  }
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
</style>
